	import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
	import empty from "@/components/empty";
	import Json from '@/Json';
    import {
        mapState
    } from 'vuex';
	export default {
		components: {
			uniLoadMore,
			empty
		},
		data() {
			return {
			    allOrders:[],
				tabCurrentIndex: 0,
				navList: [{
						state: 0,
						text: '全部',
						loadingType: 'more',
						orderList: []
					},
					{
						state: 1,
						text: '待付款',
						loadingType: 'more',
						orderList: []
					},
					{
						state: 2,
						text: '待收货',
						loadingType: 'more',
						orderList: []
					},
					{
						state: 3,
						text: '待评价',
						loadingType: 'more',
						orderList: []
					},
					{
						state: 4,
						text: '售后',
						loadingType: 'more',
						orderList: []
					}
				],
			};
		},
		
		onLoad(options){
			/**
			 * 修复app端点击除全部订单外的按钮进入时不加载数据的问题
			 * 替换onLoad下代码即可
			 */
			this.tabCurrentIndex = +options.state;




			if(options.state == 0){
				this.loadData()
			}


		},
        computed: {
            ...mapState(['userInfo'])
        },
		 
		methods: {

            navToCarts(){
                var url = '/pages/cart/cart';
                console.log(url)
                uni.switchTab({
                    url
                })
            },
            //获取订单列表
			loadData(source){
				//这里是将订单挂载到tab列表下
				//获取当前列表索引
				let index = this.tabCurrentIndex;
				//获取当前navList实体
				let navItem = this.navList[index];
				//当前navList.state more表示需要进行第一次下载
				let state = navItem.state;
				
				if(source === 'tabChange' && navItem.loaded === true){
					//tab切换只有第一次需要加载数据
					return;
				}
				if(navItem.loadingType === 'loading'){
					//防止重复加载
					return;
				}
				
				navItem.loadingType = 'loading';

				//获取后台数据接口
				var params = this.userInfo.id
                this.$http.post("user/viewAllOrders?userId="+params).then((res) => {
                    console.log(res)
					this.allOrders = res.data.data
					this.navList[0].orderList = this.allOrders
					for(var item in res.data.data){
                        if(res.data.data[item].state == 0){
                            this.navList[1].orderList.push(res.data.data[item])
						}else{
                            this.navList[2].orderList.push(res.data.data[item])
						}
					}
                }).catch(error => {
                    console.log(error)
                    return
                }).finally(() => {

                })

			}, 

			//swiper 切换
			changeTab(e){
				this.tabCurrentIndex = e.target.current;
				//this.loadData('tabChange');
			},
			//顶部tab点击
			tabClick(index){
				this.tabCurrentIndex = index;
			},
			//删除订单
			deleteOrder(fruitId,index){
			    console.log("inInCart:"+fruitId)
				uni.showLoading({
					title: '请稍后'
				})
                this.$http.post("cart/deleteSelectFruit?id="+ fruitId).then((res) => {
                    console.log("success")
                    console.log(res)
                    if (res.data.status == "success") {
                        this.$api.msg("删除成功");
                        setTimeout(()=>{
                            if(index == 0){
                                this.navList[this.tabCurrentIndex].orderList.splice(index, 1);
                                this.navList[this.tabCurrentIndex+1].orderList.splice(index, 1);
                                uni.hideLoading();
							}else if(index == 1){
                                this.navList[this.tabCurrentIndex].orderList.splice(index, 1);
                                this.navList[this.tabCurrentIndex-1].orderList.splice(index, 1);
                                uni.hideLoading();
							}
                        }, 600)
                    }else {
                        this.$api.msg(res.data.message);
                    }
                }).catch(error => {
                    console.log(error)
                    return
                }).finally(() => {

                })
			},
			//取消订单
			cancelOrder(item){
				uni.showLoading({
					title: '请稍后'
				})
				setTimeout(()=>{
					let {stateTip, stateTipColor} = this.orderStateExp(9);
					item = Object.assign(item, {
						state: 9,
						stateTip, 
						stateTipColor
					})
					
					//取消订单后删除待付款中该项
					let list = this.navList[1].orderList;
					let index = list.findIndex(val=>val.id === item.id);
					index !== -1 && list.splice(index, 1);
					
					uni.hideLoading();
				}, 600)
			},

			//订单状态文字和颜色
			orderStateExp(state){
				let stateTip = '',
					stateTipColor = '#fa436a';
				switch(+state){
					case 1:
						stateTip = '待付款'; break;
					case 2:
						stateTip = '待发货'; break;
					case 9:
						stateTip = '订单已关闭'; 
						stateTipColor = '#909399';
						break;
						
					//更多自定义
				}
				return {stateTip, stateTipColor};
			}
		},
	}
